body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Quote {
    display: flex;
    border-bottom: 2px solid #eeeeee;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    padding: 1rem;
}

.Quote-buttons{
    display: flex;
    margin-right: 1rem;
    align-items: center;
    justify-content: center;
    width: 15%;
}

.Quote-text{
    width: 75%;
    font-size: 1.2rem;
}

.Quote-votes{
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    text-align: center;
    font-size: 20px;
    font-weight: 300;
    border: 3px solid red;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px 
    rgba(0,0,0, .1);
}

.Quote-smiley img{
  width: 90px;
  height: 100px;
  border-radius: 50%;
}

.fa-arrow-up, .fa-arrow-down{
    font-size: 1.5em;
    margin: 10px;
    cursor: pointer;
}

.fa-arrow-up:hover {
    -webkit-animation-name: move-up;
            animation-name: move-up;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    color: #8bc34a;
  }

@-webkit-keyframes move-up {
  0%,
  50%,
  100% {
    transform: translateY(0);
  }
  25%,
  75% {
    transform: translateY(-3px);
  }
}

@keyframes move-up {
  0%,
  50%,
  100% {
    transform: translateY(0);
  }
  25%,
  75% {
    transform: translateY(-3px);
  }
} 

.fa-arrow-down:hover {
    -webkit-animation-name: move-down;
            animation-name: move-down;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    color: #f44336;
  }
  @-webkit-keyframes move-down {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(3px);
    }
  }
  @keyframes move-down {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(3px);
    }
  }

.Quote-smiley{
    font-size: 1rem;
    margin-left: auto; /* Auto Pushes all the way to the right */
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px 
    rgba(0,0,0, .1);
}

.Quote-smiley:hover {
    -webkit-animation-name: spin;
            animation-name: spin;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
  }
  
  @-webkit-keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    30% {
      transform: rotate(20deg);
    }
    60% {
      transform: rotate(0deg);
    }
    80% {
      transform: rotate(10deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    30% {
      transform: rotate(20deg);
    }
    60% {
      transform: rotate(0deg);
    }
    80% {
      transform: rotate(10deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
.QuoteList{
    display: flex;
    width: 80%;
    height: 80%;
}

.QuoteList-sidebar{
    background: #10375c;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    text-align: center;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px 
    rgba(0,0,0, .1) inset 0 0 25px #7e57c2;
    z-index: 2;
}

.QuoteList-title {
    font-size: 4rem;
    margin: 2rem;
    color: white;
    letter-spacing: 0.4rem;
    font-weight: 300;
}

.QuoteList-title span{
    font-weight: 700;
    letter-spacing: 0;
}

.QuoteList-sidebar img{
    width: 50%;
}

.QuoteList-sidebar img:hover {
    -webkit-animation-name: shake-slow;
            animation-name: shake-slow;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
  }

.QuoteList-quotes {
    height: 90%;
    background: white;
    align-self: center;
    width: 70%;
    overflow: scroll;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px 
    rgba(0,0,0, .1);
}

.QuoteList-spinner{
    color: #fff;
    text-align: center;
}

.QuoteList-getmore{
    font-size: 1.5rem;
    color: #fff;
    font-weight: 700;
    outline: none;
    border: none;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px 
    rgba(0,0,0, .1);
    margin: 2rem;
    width: 50%;
    border-radius: 2rem;
    padding: 1rem 2rem;
    cursor: pointer;
    transition: 2s cubic-bezier(0.2, 1. 0.2, 1);
    background-color: #844685
}

.QuoteList-getmore:hover{
    box-shadow: 0 15px 30px rgba(0, 0, 0, .5);
    transform: translateY(-4px)
}

/* Shake Animation */
@-webkit-keyframes shake-slow {
    0% {
      transform: translate(0px, 0px) rotate(0deg);
    }
    2% {
      transform: translate(5px, -6px) rotate(3.5deg);
    }
    4% {
      transform: translate(1px, -4px) rotate(2.5deg);
    }
    6% {
      transform: translate(-4px, -6px) rotate(0.5deg);
    }
    8% {
      transform: translate(2px, -9px) rotate(-0.5deg);
    }
    10% {
      transform: translate(5px, -5px) rotate(0.5deg);
    }
    12% {
      transform: translate(6px, 1px) rotate(0.5deg);
    }
    14% {
      transform: translate(5px, 2px) rotate(-1.5deg);
    }
    16% {
      transform: translate(8px, -6px) rotate(-0.5deg);
    }
    18% {
      transform: translate(-6px, -6px) rotate(3.5deg);
    }
    20% {
      transform: translate(-8px, 7px) rotate(-1.5deg);
    }
    22% {
      transform: translate(3px, 9px) rotate(1.5deg);
    }
    24% {
      transform: translate(-7px, 6px) rotate(3.5deg);
    }
    26% {
      transform: translate(9px, -5px) rotate(1.5deg);
    }
    28% {
      transform: translate(10px, 1px) rotate(-1.5deg);
    }
    30% {
      transform: translate(-8px, 5px) rotate(3.5deg);
    }
    32% {
      transform: translate(-9px, 5px) rotate(-1.5deg);
    }
    34% {
      transform: translate(-6px, 8px) rotate(0.5deg);
    }
    36% {
      transform: translate(6px, -2px) rotate(0.5deg);
    }
    38% {
      transform: translate(3px, 3px) rotate(3.5deg);
    }
    40% {
      transform: translate(6px, 10px) rotate(-1.5deg);
    }
    42% {
      transform: translate(2px, -2px) rotate(1.5deg);
    }
    44% {
      transform: translate(-6px, 8px) rotate(-0.5deg);
    }
    46% {
      transform: translate(-5px, 9px) rotate(-2.5deg);
    }
    48% {
      transform: translate(-4px, 2px) rotate(3.5deg);
    }
    50% {
      transform: translate(-9px, -3px) rotate(-1.5deg);
    }
    52% {
      transform: translate(3px, -2px) rotate(1.5deg);
    }
    54% {
      transform: translate(10px, 8px) rotate(3.5deg);
    }
    56% {
      transform: translate(7px, 8px) rotate(0.5deg);
    }
    58% {
      transform: translate(-4px, -3px) rotate(-0.5deg);
    }
    60% {
      transform: translate(9px, -4px) rotate(-1.5deg);
    }
    62% {
      transform: translate(-8px, -6px) rotate(1.5deg);
    }
    64% {
      transform: translate(-1px, -2px) rotate(3.5deg);
    }
    66% {
      transform: translate(0px, -8px) rotate(0.5deg);
    }
    68% {
      transform: translate(8px, -7px) rotate(0.5deg);
    }
    70% {
      transform: translate(3px, 4px) rotate(1.5deg);
    }
    72% {
      transform: translate(3px, 2px) rotate(1.5deg);
    }
    74% {
      transform: translate(-5px, -9px) rotate(0.5deg);
    }
    76% {
      transform: translate(1px, -8px) rotate(2.5deg);
    }
    78% {
      transform: translate(3px, 7px) rotate(-1.5deg);
    }
    80% {
      transform: translate(-9px, -4px) rotate(0.5deg);
    }
    82% {
      transform: translate(0px, -4px) rotate(-2.5deg);
    }
    84% {
      transform: translate(-7px, -3px) rotate(0.5deg);
    }
    86% {
      transform: translate(-8px, 6px) rotate(-1.5deg);
    }
    88% {
      transform: translate(3px, 4px) rotate(2.5deg);
    }
    90% {
      transform: translate(5px, 7px) rotate(2.5deg);
    }
    92% {
      transform: translate(2px, -8px) rotate(-2.5deg);
    }
    94% {
      transform: translate(-1px, -2px) rotate(-1.5deg);
    }
    96% {
      transform: translate(-4px, 10px) rotate(-1.5deg);
    }
    98% {
      transform: translate(10px, -9px) rotate(2.5deg);
    }
  }
